.App {
    text-align: center;
    display: flex;
    justify-content: center;
}
.card {
    position: relative;
    width: 70%;
    height: 70%;
    margin-bottom: 10px;
    margin-top: 1%;
    background: transparent;
    background-size: cover;
    padding: 20px;
    border: 1px solid #fff;
    border-radius: 20px;
}
.title {
    color: #fff;
    font-weight: 800;
    font-size: 30px;
    i {
        margin-right: 10px;
    }
}
.search-form {
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    input {
        width: 90%;
        height: 40px;
        padding: 5px;
        border: 1px solid #fff;
        background: transparent;
        color: #fff;
        border-radius: 10px;
        margin: 0 auto;
    }
    button {
        width: 8%;
        height: 50px;
        padding: 5px;
        border: none;
        border-radius: 10px;
        background: linear-gradient(45deg, #8e0e00 30%, #1f1c18 90%);
        font-weight: 400;
        font-size: 18px;
        color: #fff;
        cursor: pointer;
        &:hover {
            background: linear-gradient(45deg, #ffaf8d 30%, #ffc3a0 90%);
            color: #000;
        }
    }
}
.main-container {
    width: 100%;
    height: auto;
    color: #fff;
    text-align: center;
    margin-top: 30px;
    h4 {
        font-size: 25px;
        font-weight: 800;
    }
    .weather-icon img {
        width: 100px;
        height: 100px;
        animation: iconanimation 3s infinite;
    }
}
@keyframes iconanimation {
    from {
        transform: scale(0.5, 0.5);
    } to {
    transform: scale(1,1);
          }
}
@media only screen and (min-width: 600px) {
    .search-form input {
        width: 70%;
    }
    .search-form button {
        width: 25%;
    }
}
@media only screen and (max-width: 600px) {
    .search-form input {
        width: 70%;
    }
    .search-form button {
        width: 25%;
    }
}
@media only screen and (min-width: 768px) {
    .search-form input {
        width: 80%;
    }
    .search-form button {
        width: 18%;
    }
}
@media only screen and (min-width: 992px) {
    .search-form input {
        width: 80%;
    }
    .search-form button {
        width: 18%;
    }
}
@media only screen and (min-width: 1200px) {
    .search-form input {
        width: 90%;
    }
    .search-form button {
        width: 8%;
    }
}
.temperature h1 {
    font-size: 30px;
    font-weight: 900;
}
.location h3 i {
    margin-right: 10px;
    animation: locationicon 2s infinite;
}
@keyframes locationicon {
    0% {
        transform: rotateY(0deg);
    }
    50% {
        transform: rotateY(180deg);
    }
    100% {
        transform: rotateY(360deg);
    }
}
.loader-container {
    margin: 15%;
}